import {SsieOutputObject} from "../types/SsieOutputObject";
import {StringMap} from "../types/StringMap";
import PDFPageInfo from "../types/PDFPageInfo";
import {Block} from "../types/Block";
import {PropertyDataStore} from "../../../store/PropertyDataStore";
import {Annotation, SsieAnnotations} from "../context/SsieAnnotationStore";
import {BlockReference, ChildBlock, Entity, PropertiesType} from "../types/Entity";
import {BlockDescriptor} from "../types/BlockDescriptor";
import {addBlockIndices, BLOCK_TYPE, DOCUMENT_TYPE, getBlockBounds} from "./SsieUtils";


// IMPORT

export const getSsieAnnotationsFromEntities = (
    annotationProp: SsieOutputObject,
    pageInfo: PDFPageInfo,
    primaryPdfBlocks: Block[],
    propertyDataStore: PropertyDataStore
) => {
    let ssieAnnotations: SsieAnnotations = new SsieAnnotations({}, {}, {}, new Set<string>());
    const pdfBlocks = annotationProp.Blocks && annotationProp.Blocks.length > 0 ? addBlockIndices(annotationProp.Blocks!).filter(block => block.BlockType === BLOCK_TYPE.WORD) : primaryPdfBlocks;

    for (let i = 0; i < pdfBlocks.length; i++) {
        if ((i < primaryPdfBlocks.length ? primaryPdfBlocks[i].Text : '') !== pdfBlocks[i].Text) {
            pdfBlocks[i].invalid = true;
        }
    }

    const blocksIdxMap = pdfBlocks.reduce((map: StringMap<number>, block, idx) => {
        map[block.Id] = idx;
        return map;
    }, {});
    for (const entity of annotationProp.Entities) {
        let entityAnnotations: Annotation[] = [];
        let relationAnnotationIds: string[] = [];
        let mismatchBlocks: boolean = false;
        for (const blockReference of entity.BlockReferences) {
            // if a mismatch is found between the primary and secondary blocks, skip this secondary annotation's entity
            if (mismatchBlocks) {
                entityAnnotations = [];
                relationAnnotationIds = [];
                break;
            }
            const partialBlockDescriptor = [];
            for (const childBlock of blockReference.ChildBlocks) {

                partialBlockDescriptor.push({
                    index: blocksIdxMap[childBlock.ChildBlockId],
                    startOffset: childBlock.BeginOffset,
                    endOffset: childBlock.EndOffset,
                });
            }
            const annotation = pageInfo.createAnnotationFromPartialBlockDescriptor(partialBlockDescriptor, entity.Type, pdfBlocks);
            if (annotation) {
                entityAnnotations.push(annotation);
                relationAnnotationIds.push(annotation.id);
            } else {
                mismatchBlocks = true;
            }
        }
        // Populate property Data Store (picks the zeroth annotation to link the property for simplicity
        updatePropertyDataStore(propertyDataStore, relationAnnotationIds[0], entity.Type, entity.Properties);
        ssieAnnotations = ssieAnnotations.updateAnnotations(entityAnnotations);
        if (relationAnnotationIds.length > 1) {
            ssieAnnotations = ssieAnnotations.withNewRelation(relationAnnotationIds);
        }
    }
    return ssieAnnotations;
};

const updatePropertyDataStore = (propertyDataStore: PropertyDataStore,
                                 id: string, tag: string,
                                 properties: PropertiesType | undefined) => {
    if (properties !== undefined) {
        // properties exist;
        // create instance store
        const instanceStore = propertyDataStore.getInstanceStore({
            id: id,
            tag: tag
        });
        // restore properties
        if (instanceStore !== undefined) {
            instanceStore.load(properties);
        }
    }
};

// EXPORT

export const ssieAnnotationsToOutputObject = (
    blocks: Block[],
    allBlocks: Block[],
    pdfBlocksS3Ref: string,
    ssieAnnotations: SsieAnnotations,
    propertyDataStore: PropertyDataStore,
    pdfType: DOCUMENT_TYPE,
    version: string,
    metadata: StringMap<string>
): SsieOutputObject => {
    const { annotations, discontiguousRelations } = ssieAnnotations;

    const allRelationAnnotationIds = new Set<string>();

    const relationEntities = Object.values(discontiguousRelations).map((rel) => {
        const relBlockIds: BlockDescriptor[] = [];
        let tag: string | undefined = '';
        for (const annoId of rel.annotationIds) {
            allRelationAnnotationIds.add(annoId);
            const anno = annotations[annoId] as Annotation;
            if (!tag) {
                tag = anno.tag;
            }
            Array.prototype.push.apply(relBlockIds, anno.blockDescriptors);
        }
        return annotationBlockDescriptorsToEntity(relBlockIds, tag!, blocks, allBlocks, propertyDataStore, rel.annotationIds);
    });

    const entities: Entity[] = Object.values(annotations)
        .filter((anno) => !allRelationAnnotationIds.has(anno.id))
        .map((anno) => annotationBlockDescriptorsToEntity(anno.blockDescriptors, anno.tag, blocks, allBlocks, propertyDataStore, [anno.id]));

    return {
        Version: version,
        DocumentType: pdfType,
        DocumentMetadata: {
            Pages: metadata['pages'],
            Page: metadata['page'],
        },
        BlocksS3Ref: pdfBlocksS3Ref,
        Entities: [...entities, ...relationEntities],
    };
};

export const annotationBlockDescriptorsToEntity = (annotationBlockDescriptors: BlockDescriptor[], tag: string,
                                           wordBlocks: Block[], allBlocks: Block[],
                                           propertyDataStore: PropertyDataStore,
                                           relationAnnotationIds: string[]) => {
    const finalText: string[] = [];

    // partition blocks by line
    const annoBlockIndicesParentPartitioned: {
        blockIndex: number;
        startOffset: number;
        endOffset: number;
    }[][] = [];
    let annoBlockIndices: { blockIndex: number; startOffset: number; endOffset: number }[] = [];
    let currParentIndex = -1;
    let currBlockIndex = -1;
    for (const blockDescriptor of annotationBlockDescriptors) {
        const { blockIndex, startOffset, endOffset } = blockDescriptor;
        const parentIndex = wordBlocks[blockIndex].parentBlockIndex;
        if (parentIndex !== currParentIndex || currBlockIndex + 1 !== blockIndex) {
            if (annoBlockIndices.length > 0) {
                annoBlockIndicesParentPartitioned.push(annoBlockIndices);
            }
            annoBlockIndices = [];
            currParentIndex = parentIndex!;
        }
        annoBlockIndices.push({
            blockIndex: wordBlocks[blockIndex].blockIndex!,
            startOffset,
            endOffset,
        });
        currBlockIndex = blockIndex;
    }
    if (annoBlockIndices.length > 0) {
        annoBlockIndicesParentPartitioned.push(annoBlockIndices);
    }

    const blockReferenes: BlockReference[] = annoBlockIndicesParentPartitioned.map((annoBlockIndices) => {
        const { blockIndex: allBlocksIndex } = annoBlockIndices[0];
        const { parentBlockIndex: parentAllBlockIndex } = allBlocks[allBlocksIndex];

        const parentAllBlock = allBlocks[parentAllBlockIndex!];

        const childAllBlock: Block[] = [];
        const childBlocks: ChildBlock[] = [];
        for (const obj of annoBlockIndices) {
            const { blockIndex, startOffset, endOffset } = obj;
            const childBlock = allBlocks[blockIndex];
            childAllBlock.push(childBlock);
            childBlocks.push({
                BeginOffset: startOffset,
                EndOffset: endOffset,
                ChildBlockId: childBlock.Id,
            });
        }
        const childAllBlockText = childAllBlock.map((b, i) => b.Text.substring(childBlocks[i].BeginOffset, childBlocks[i].EndOffset)).join(' ');

        finalText.push(childAllBlockText);
        // get beginOffset of entity first block in line
        const beginOffset = getChildBlockOffsetInLineBlock(allBlocks, parentAllBlock, childAllBlock[0]) + childBlocks[0].BeginOffset;
        // endOffset of entity last block in line
        const endOffset =
            getChildBlockOffsetInLineBlock(allBlocks, parentAllBlock, childAllBlock[childAllBlock.length - 1]) +
            childBlocks[childAllBlock.length - 1].EndOffset;
        return {
            BlockId: parentAllBlockIndex === -1 ? childAllBlock[0].Id : parentAllBlock.Id,
            ChildBlocks: childBlocks,
            BeginOffset: beginOffset,
            EndOffset: endOffset,
        };
    });
    const entity: Entity = {
        BlockReferences: blockReferenes,
        Text: finalText.join(' '),
        Type: tag,
        Score: 1,
    }
    const properties = getExportedPropertiesForEntity(propertyDataStore, relationAnnotationIds, tag);
    if (properties) {
        entity.Properties = properties;
    }
    return entity;
};


const getChildBlockOffsetInLineBlock = (allBlocks: Block[], parentAllBlock: Block, searchBlock: Block) => {
    const allBlocksMap: StringMap<Block> = {};
    for (const block of allBlocks) {
        allBlocksMap[block.Id] = block;
    }
    let runningOffset = 0;
    for (const childBlockId of parentAllBlock.Relationships[0].Ids) {
        if (childBlockId === searchBlock.Id) {
            return runningOffset;
        } else {
            runningOffset += allBlocksMap[childBlockId].Text.length + 1;
        }
    }
    throw new Error('SsieUtils::getChildBlockOffsetInLineBlock error');
};

const getExportedPropertiesForEntity = (propertyDataStore: PropertyDataStore,
                                        relationAnnotationIds: string[],
                                        tag: string): PropertiesType | undefined => {
    for (const annotationId of relationAnnotationIds) {
        const properties = propertyDataStore.getSpanPropertyAnnotations({
            id: annotationId,
            tag: tag
        })
        if (properties) {
            return properties;
        }
    }
    return undefined;
}

/**
 * Test method for converting a block to an annotation
 * Ex. usage in ssieAnnotationsToOutputObject():
 *  const testAnnotations = pdfBlocks.slice(0,500).map((block, i) => blockToAnnotation(block, i, pageInfo));
 *  ssieAnnotations = ssieAnnotations.updateAnnotations(testAnnotations);
 * @param block 
 * @param blockIndex 
 * @param pdfPageInfo 
 * @returns Annotation with dummy tag for given block
 */
 export const blockToAnnotation = (block: Block, blockIndex: number, pdfPageInfo: PDFPageInfo): Annotation => {
    const blockDescriptor = new BlockDescriptor(
        pdfPageInfo.pdfPageProxy.pageNumber - 1,
        blockIndex,
        block.BlockType,
        0,
        block.Text.length
    )
    return new Annotation(getBlockBounds(block, pdfPageInfo.width, pdfPageInfo.height), pdfPageInfo.pdfPageProxy.pageNumber, ["TAG1", "TAG2", "TAG3"][Math.floor(Math.random() * Math.floor(3))], [blockDescriptor]);
}