import {
    Annotation,
    areSameTag,
    DiscontiguousRelation,
    SsieAnnotations
} from '../context/SsieAnnotationStore';
import {Block} from '../types/Block';
import {Bounds} from '../types/Bounds';
import {BlockDescriptor} from '../types/BlockDescriptor';
import {StringMap} from '../types/StringMap';
import {v4 as uuidv4} from 'uuid';
import {UNASSIGNED_TAG} from '../../../store/TaggingStateStore';

export const getMultipleAnnotationText = (annotations: Annotation[], pdfBlocks: Block[]) => {
    return annotations.map(anno => getAnnotationText(anno, pdfBlocks).join(' '));
}

export const getAnnotationText = (annotation: Annotation, pdfBlocks: Block[]) => {
    return annotation.blockDescriptors.map((blockDescriptor) => pdfBlocks[blockDescriptor.blockIndex].Text.substring(blockDescriptor.startOffset, blockDescriptor.endOffset));
};

export type Optional<T> = T | undefined;

export const getBinaryFromPdfBase64 = (pdfBase64: string) => {
    var raw = window.atob(pdfBase64);
    var rawLength = raw.length;
    var array = new Uint8Array(new ArrayBuffer(rawLength));

    for (var i = 0; i < rawLength; i++) {
        array[i] = raw.charCodeAt(i);
    }
    return array;
};

export const getElementPosition = (element: Element) => {
    const rect = element.getBoundingClientRect();
    return { x: rect.left, y: rect.top };
};

export function getBlockBounds(block: Block, width: number, height: number): Bounds {
    return {
        left: block.Geometry.BoundingBox.Left * width,
        top: block.Geometry.BoundingBox.Top * height,
        right: (block.Geometry.BoundingBox.Left + block.Geometry.BoundingBox.Width) * width,
        bottom: (block.Geometry.BoundingBox.Top + block.Geometry.BoundingBox.Height) * height,
    };
}

/**
 * Computes a bound which contains all of the bounds passed as arguments.
 */
export function spanningBound(bounds: Bounds[], padding: number = 3): Bounds {
    // Start with a bounding box for which any bound would be
    // contained within, meaning we immediately update maxBound.
    const maxBound: Bounds = {
        left: Number.MAX_VALUE,
        top: Number.MAX_VALUE,
        right: 0,
        bottom: 0,
    };

    bounds.forEach((bound) => {
        maxBound.bottom = Math.max(bound.bottom, maxBound.bottom);
        maxBound.top = Math.min(bound.top, maxBound.top);
        maxBound.left = Math.min(bound.left, maxBound.left);
        maxBound.right = Math.max(bound.right, maxBound.right);
    });

    maxBound.top = maxBound.top - padding;
    maxBound.left = maxBound.left - padding;
    maxBound.right = maxBound.right + padding;
    maxBound.bottom = maxBound.bottom + padding;

    return maxBound;
}

export const isRightClick = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    return ('which' in e && (e as any).which === 3) || ('button' in e && e.button === 2);
};

export enum BLOCK_TYPE {
    WORD = 'WORD',
    LINE = 'LINE',
}

export enum DOCUMENT_TYPE {
    ScannedPDF = 'ScannedPDF',
    NativePDF = 'NativePDF',
}

export function flattenArray<T>(a: T[][]): T[] {
    return a.reduce((acc, val) => acc.concat(val), []);
};

export const disableEnterKey = (e: React.KeyboardEvent<any>) => {
    if (e.keyCode === 13) {
        e.preventDefault();
        e.stopPropagation();
    }
};

export const addBlockIndices = (blocks: Block[]) => {
    let currParentBlockIndex = 0;
    return blocks.map((block, idx) => {
        if (block.BlockType === BLOCK_TYPE.WORD) {
            if (block.parentBlockIndex && block.blockIndex) {
                return block;
            }
            return {
                ...block,
                parentBlockIndex: currParentBlockIndex,
                blockIndex: idx,
            };
        } else {
            currParentBlockIndex = idx;
            if (block.parentBlockIndex && block.blockIndex) {
                return block;
            }
            return {
                ...block,
                parentBlockIndex: -1,
                blockIndex: idx,
            };
        }
    });
};

export const getAnnotationBlockIndices = (annotations: Annotation[]) => {
    return flattenArray(annotations.map(annotation => annotation.blockDescriptors.map((blockDescriptor) => blockDescriptor.blockIndex)));
};

export const annotationBlockDescriptorsEqual = (blockDescriptor1: BlockDescriptor[], blockDescriptor2: BlockDescriptor[]) => {
    if (blockDescriptor1.length !== blockDescriptor2.length) {
        return false;
    }
    for (let i = 0; i < blockDescriptor1.length; i++) {
        if (
            blockDescriptor1[i].blockIndex !== blockDescriptor2[i].blockIndex ||
            blockDescriptor1[i].startOffset !== blockDescriptor2[i].startOffset ||
            blockDescriptor1[i].endOffset !== blockDescriptor2[i].endOffset
        ) {
            return false;
        }
    }
    return true;
};

export const annotationsEqual = (anno1: Annotation, anno2: Annotation) => {
    return anno1.tag === anno2.tag && annotationBlockDescriptorsEqual(anno1.blockDescriptors, anno2.blockDescriptors);
};

export const copyAnnotation = (annotation: Annotation, newId: boolean = false): Annotation => {
    return {
        bounds: {
            bottom: annotation.bounds.bottom,
            left: annotation.bounds.left,
            right: annotation.bounds.right,
            top: annotation.bounds.top,
        },
        id: newId ? uuidv4() : annotation.id,
        page: annotation.page,
        tag: annotation.tag,
        blockDescriptors: annotation.blockDescriptors.map((t) => ({...t})),
        relationId: null,
        inStagedRelation: false
    };
};

/**
 * Handle selecting an annotation from the pdf viewer or with controls
 * 1. select annotation from pdf viewer
 *  a. annotation is part of discontiguous relation - select discontiguous relation annotations and set stagedRelation to empty list
 *  b. annotation is NOT part of discontiguous relation - select annotation and set as staged relation
 * 2. select annotation from controls
 *  a. select overlap - select all overlapping annotations and set as staged relation if tags are all the same
 *  b. NOT select overlap - select annotation and set as staged relation
 */
export const handleAnnotationSelect = ({
    setSsieAnnotationStore,
    ssieAnnotations,
    annotation,
    selectOverlap = true,
    fromControls = false
}: {
    setSsieAnnotationStore: any;
    ssieAnnotations: SsieAnnotations;
    annotation: Annotation;
    selectOverlap?: boolean;
    fromControls?: boolean;
}) => {
    const existingAnnotation = ssieAnnotations.annotations[annotation.id];
    if (fromControls) {
        // select annotation from controls
        if (selectOverlap) {
            // select overlap - select all overlapping annotations and set as staged relation if tags are all the same
            const selectedAnnotations = Object.values(ssieAnnotations.annotations).filter((anno) => {
                if (anno.id === annotation.id) {
                    return true;
                }
                const currAnnoTokens = new Set(anno.blockDescriptors.map((t) => t.blockIndex));
                return annotation.blockDescriptors.every((t) => currAnnoTokens.has(t.blockIndex));
            });
            setSsieAnnotationStore({
                selectedSsieAnnotations: selectedAnnotations,
                stagedRelation: annotationIsNotUnassigned(selectedAnnotations[0]) && areSameTag(selectedAnnotations) ? selectedAnnotations.map(anno => anno.id) : [],
            });
        } else {
            // NOT select overlap - select annotation and set as staged relation
            setSsieAnnotationStore({
                selectedSsieAnnotations: [existingAnnotation],
                stagedRelation: annotationIsNotUnassigned(existingAnnotation) ? [existingAnnotation.id] : [],
            });
        }
    } else {
        // select annotation from pdf viewer
        if (annotation.relationId) {
            // annotation is part of discontiguous relation - select discontiguous relation annotations and set stagedRelation to empty list
            setSsieAnnotationStore({
                selectedSsieAnnotations: getDiscontiguousRelationAnnotations(ssieAnnotations, annotation.relationId),
                stagedRelation: [],
            });
        } else {
            // annotation is NOT part of discontiguous relation - select annotation and set as staged relation
            setSsieAnnotationStore({
                selectedSsieAnnotations: [existingAnnotation],
                stagedRelation: annotationIsNotUnassigned(existingAnnotation) ? [existingAnnotation.id] : [],
            });
        }
    }
};

/**
 * Handle the selection of multiple annotations (will only be called if annotation is NOT part of a discontiguous relation).
 *  1. No annotations are selected - select annotation and add to staged relation if annotation has tag
 *  2. Annotations are selected
 *      - existing selected annotations are part of a discontiguous relation - select annotation and set staged relation to annotation if annotation has !Unassigned tag
 *      - existing selected annotations are NOT part of a discontiguous relation
 *          1. annotation tag is the same as the other tags in existing selected annotations and all tags are not unassigned - add annotation id to staged relation if annotation has !Unassigned tag
 *          2. annotation tag is NOT the same as other tags in existing selected annotations - set staged relation to empty
 */
export const handleAnnotationMultiSelect = ({
    setSsieAnnotationStore,
    selectedSsieAnnotations,
    stagedRelation,
    annotation,
}: {
    setSsieAnnotationStore: any;
    selectedSsieAnnotations: Annotation[];
    stagedRelation: string[];
    annotation: Annotation;
}) => {
    if (selectedSsieAnnotations.length > 0) {
        // 2. Annotations are selected
        const clickedAnnotationIsNotAlreadySelected =  selectedSsieAnnotations.filter(anno => anno.id !== annotation.id).length === selectedSsieAnnotations.length;
        if (clickedAnnotationIsNotAlreadySelected) {
            if (selectedSsieAnnotations[0].relationId) {
                // - existing selected annotations are part of a discontiguous relation - select annotation and set staged relation to annotation if annotation has !Unassigned tag
                setSsieAnnotationStore({
                    selectedSsieAnnotations: [annotation],
                    stagedRelation: annotationIsNotUnassigned(annotation) ? [annotation.id] : [],
                });
            } else {
                // - annotation is NOT part of a discontiguous relation - add annotation to selected annotations
                const newSelectedAnnotations = [...selectedSsieAnnotations, annotation];
                // 1. annotation tag is the same as the other tags in existing selected annotations and all tags are not unassigned - add annotation id to staged relation if annotation has !Unassigned tag
                // 2. annotation tag is NOT the same as other tags in existing selected annotations - set staged relation to empty
                const newStagedRelation = annotationIsNotUnassigned(annotation) && annotation.tag === selectedSsieAnnotations[0].tag ? [...stagedRelation, annotation.id] : [];
                setSsieAnnotationStore({
                    selectedSsieAnnotations: newSelectedAnnotations,
                    stagedRelation: newStagedRelation
                });
            }
        }
    } else {
        // 1. No annotations are selected - select annotation and add to staged relation if annotation has tag
        setSsieAnnotationStore({
            selectedSsieAnnotations: [annotation],
            stagedRelation: annotationIsNotUnassigned(annotation) ? [annotation.id] : [],
        });
    }
};

/**
 * Update tags for selected annotations in annotation store.
 * @param tag Tag to update selected annotations with
 * @param param1 
 */
export const updateSelectedSsieAnnotationsProperties = (
    tag: string,
    {
        selectedSsieAnnotations,
        ssieAnnotations,
        setSsieAnnotationStore,
    }: { selectedSsieAnnotations: Annotation[]; ssieAnnotations: SsieAnnotations; setSsieAnnotationStore: any }
) => {
    if (selectedSsieAnnotations.length > 0) {
        const updatedSelectedAnnotationsIds: string[] = [];
        const updatedSelectedAnnotations = selectedSsieAnnotations.map(annotation => {
            const copiedAnno = copyAnnotation(annotation);
            updatedSelectedAnnotationsIds.push(copiedAnno.id);
            return { ...ssieAnnotations.annotations[copiedAnno.id], tag };
        });
        setSsieAnnotationStore({
            ssieAnnotations: ssieAnnotations.updateAnnotations(updatedSelectedAnnotations),
            selectedSsieAnnotations: updatedSelectedAnnotations,
            stagedRelation: annotationIsNotUnassigned(updatedSelectedAnnotations[0]) && areSameTag(updatedSelectedAnnotations) ? updatedSelectedAnnotationsIds: []
        });
    }
};

export const removeActiveSsieAnnotations = (selectedSsieAnnotations: Annotation[], ssieAnnotations: SsieAnnotations, setSsieAnnotationStore: any) => {
    if (selectedSsieAnnotations.length > 0) {
        setSsieAnnotationStore({
            ssieAnnotations: ssieAnnotations.deleteAnnotations(selectedSsieAnnotations),
            selectedSsieAnnotations: [],
            stagedRelation: [],
        });
    }
};

/**
 * Handle changes to ssie annotation store components when clicking the connect/disconnect button
 * - CONNECT: add a discontiguous relation from the selected annotations and deselect all annotations
 * - DISCONNECT: delete the discontiguous relation, remove the relation id from the selected annotations, and set the selected annotations as staged
 * @param selectedSsieAnnotations 
 * @param ssieAnnotations 
 * @param stagedRelation 
 */
export const handleConnectButtonClick = (
    selectedSsieAnnotations: Annotation[],
    ssieAnnotations: SsieAnnotations,
    stagedRelation: string[]
) => {
    return {
        ssieAnnotations: selectedSsieAnnotations[0].relationId ?
            ssieAnnotations.deleteRelations([selectedSsieAnnotations[0].relationId]) :
            ssieAnnotations.withNewRelation(stagedRelation),
        selectedSsieAnnotations: [],
        stagedRelation: [],
    }
};

export const groupAnnotationsIntoEntities = (ssieAnnotations: SsieAnnotations) => {
    const relationAnnotationIds = new Set<string>();
    const discontiguousEntities = [];
    for (const discontiguousRelation of Object.values(ssieAnnotations.discontiguousRelations)) {
        const annotations = [];
        for (const annotationId of discontiguousRelation.annotationIds) {
            relationAnnotationIds.add(annotationId);
            annotations.push(ssieAnnotations.annotations[annotationId]);
        }
        if (annotations.every(anno => anno)) {
            discontiguousEntities.push({ annotations, relationId: discontiguousRelation.id });
        }
    }
    const entities = Object.values(ssieAnnotations.annotations).filter(anno => !relationAnnotationIds.has(anno.id)).map(anno => ({ annotations: [anno], relationId: null }));
    return [...discontiguousEntities, ...entities];
};

export const getBoundsForBlocks = (blocks: Block[], width: number, height: number) => {
    const bounds = blocks.map((b) => getBlockBounds(b, width, height));
    return spanningBound(bounds);
};

/**
 * Decide whether to show connect/disconnect button.
 * @param selectedSsieAnnotations 
 * @param stagedRelation 
 * @returns boolean
 */
export const showConnectButton = (selectedSsieAnnotations: Annotation[], stagedRelation: string[]) =>
    selectedSsieAnnotations.length > 1 && (!!selectedSsieAnnotations[0].relationId || stagedRelation.length > 1);

/**
 * Update inStagedRelation attributes and return annotations that have been updated
 * @param oldStagedRelation list of annotation ID's in original staged relation
 * @param newSsieAnnotations ssieAnnotation object for new staged relation
 * @param newStagedRelation list of annotation ID's in new staged relation 
 */
export const getUpdatedStagedRelationAnnotations = (oldStagedRelation: string[], newSsieAnnotations: SsieAnnotations, newStagedRelation: string[]) => {
    const newStagedRelationAnnotationDict: StringMap<Annotation> = {};
    if (oldStagedRelation.length > 0) {
        for (const stagedRelationAnnotationId of oldStagedRelation) {
            const updateInStagedRelationAnnotation = newSsieAnnotations.annotations[stagedRelationAnnotationId];
            if (updateInStagedRelationAnnotation) {
                updateInStagedRelationAnnotation.inStagedRelation = false;
                newStagedRelationAnnotationDict[stagedRelationAnnotationId] = updateInStagedRelationAnnotation;
            }
        }
    }
    if (newStagedRelation.length > 0) {
        for (const stagedRelationAnnotationId of newStagedRelation) {
            const updateInStagedRelationAnnotation = newSsieAnnotations.annotations[stagedRelationAnnotationId];
            updateInStagedRelationAnnotation.inStagedRelation = true;
            newStagedRelationAnnotationDict[stagedRelationAnnotationId] = updateInStagedRelationAnnotation;
        }
    }
    return Object.values(newStagedRelationAnnotationDict);
}

export const discontiguousRelationEquals = (
    primaryRelation: DiscontiguousRelation, 
    primarySsieAnnotation: SsieAnnotations, 
    secondaryRelation: DiscontiguousRelation, 
    secondarySsieAnnotation: SsieAnnotations
) => {
    const equalAnnotationCount = primaryRelation.annotationIds.length === secondaryRelation.annotationIds.length;
    return equalAnnotationCount && primaryRelation.annotationIds.every((annoId, i) =>
        annotationsEqual(primarySsieAnnotation.annotations[annoId], secondarySsieAnnotation.annotations[secondaryRelation.annotationIds[i]]));
}

/**
 * Uses annotation ID to search for an annotation within a list of annotations
 * @param annotations 
 * @param searchAnnotation 
 * @returns whether the annotation's ID can be found among the list of annotations' ids
 */
export const annotationsIncludes = (annotations: Annotation[], searchAnnotation: Annotation) => {
    return new Set(annotations.map(({ id }) => id)).has(searchAnnotation.id);
}

/**
 * Get list of annotations from discontiguous relation
 * @param ssieAnnotations 
 * @param discontiguousRelationId 
 */
export const getDiscontiguousRelationAnnotations = (ssieAnnotations: SsieAnnotations, discontiguousRelationId: string) => {
    const discontiguousRelation = ssieAnnotations.discontiguousRelations[discontiguousRelationId];
    return discontiguousRelation ? discontiguousRelation.annotationIds.map(id => ssieAnnotations.annotations[id]) : [];
}

export const annotationIsNotUnassigned = (annotation: Annotation) => {
    return annotation.tag !== UNASSIGNED_TAG;
}

export const addDiscontiguousRelationAnnotations = (annotations: Annotation[], ssieAnnotations: SsieAnnotations) => {
    const newAnnotations: Annotation[] = [];
    const addedRelationIds = new Set<string>();
    for (const annotation of annotations) {
        if (annotation.relationId) {
            if (!addedRelationIds.has(annotation.relationId)) {
                newAnnotations.push(...getDiscontiguousRelationAnnotations(ssieAnnotations, annotation.relationId));
                addedRelationIds.add(annotation.relationId);
            }
        } else {
            newAnnotations.push(annotation);
        }
    }
    return newAnnotations;
}

export const getEntityBlockDescriptors = (annotation: Annotation, ssieAnnotations: SsieAnnotations) => {
    let entityBlockDescriptors = annotation.blockDescriptors;
    if (annotation.relationId) {
        const discontiguousRelationAnnotations = getDiscontiguousRelationAnnotations(ssieAnnotations, annotation.relationId);;
        entityBlockDescriptors = flattenArray(discontiguousRelationAnnotations.map(anno => anno.blockDescriptors));
    }
    return entityBlockDescriptors;
}