import React, { useContext } from 'react';
import { ConnectTagColor, RedSpanColor, SecondarySpanColor } from '../../../../../../AnnotatorCommons';
import { UNASSIGNED_TAG } from '../../../../../../store/TaggingStateStore';
import {
    Annotation,
    getEntityConnectName,
    SsieAnnotationStore,
} from '../../../../context/SsieAnnotationStore';
import { StringMap } from '../../../../types/StringMap';
import { getAnnotationBlockIndices } from '../../../../utils/SsieUtils';

interface Props {
    isSecondary?: boolean;
    annotation: Annotation;
    onMouseDown: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => void;
    onContextMenuClick: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void;
    hasAnnotationDifference: boolean;
}

/**
 * Component for the connection label of a semi-structured annotation where connection label is a shared label between all spans of a discontiguous entity.
 * Screenshot: https://tiny.amazon.com/6jwp5ka7/drivcorpamazdocudnleCRpng
 */
const ConnectionLabel: React.FC<Props> = ({
    isSecondary,
    annotation,
    onMouseDown,
    onContextMenuClick,
    hasAnnotationDifference,
}) => {
    const { ssieAnnotations, stagedRelation, selectedSsieAnnotations } = useContext(SsieAnnotationStore);

    const [adjustConnectionLabelForOverlap, setAdjustConnectionLabelForOverlap] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (isSecondary && annotation.relationId) {
            setAdjustConnectionLabelForOverlap(secondaryAnnotationSetAdjustConnectionLabelForOverlap(annotation, ssieAnnotations.annotations, ssieAnnotations.blockIndexToAnnotationIds, selectedSsieAnnotations));
        } else {
            setAdjustConnectionLabelForOverlap(false);
        }
    }, [ssieAnnotations.blockIndexToAnnotationIds, ssieAnnotations.annotations, stagedRelation, isSecondary, annotation, selectedSsieAnnotations]);

    const stagedRelationIndex = stagedRelation.findIndex((id) => id === annotation.id);
    const showStagedConnectionLabel = annotation.tag !== UNASSIGNED_TAG && stagedRelation.length > 1 && stagedRelationIndex > -1;

    return annotation.relationId ? (
        <span
            className={`selection-boundary-span-tag ConnectTagStyle NoSelect`}
            onMouseDown={onMouseDown}
            onContextMenu={onContextMenuClick}
            style={{
                color: `${hasAnnotationDifference ? RedSpanColor : isSecondary ? SecondarySpanColor : ConnectTagColor}`,
                bottom: `${adjustConnectionLabelForOverlap ? '-30px' : '-15px'}`,
            }}
        >
            {getEntityConnectName(annotation.relationId)}
        </span>
    ) : showStagedConnectionLabel ? (
        <span
            className={`selection-boundary-span-tag ConnectTagStyle NoSelect`}
            onMouseDown={onMouseDown}
            onContextMenu={onContextMenuClick}
            style={{ color: ConnectTagColor }}
        >
            {`ARG_${stagedRelationIndex}`}
        </span>
    ) : null;
};

/**
 * Check if secondary annotation's connection label needs to be adjusted for overlap
 * Adjust connection label for overlap if:   
 *  a. primary annotation overlap has connection
 *  b. primary annotation is in staged relation
 * @param annotation secondary annotation
 * @param annotations map of primary annotations
 * @param blockIndexToAnnotationIds 
 * @param selectedSsieAnnotations 
 * @returns 
 */
export const secondaryAnnotationSetAdjustConnectionLabelForOverlap = (
    annotation: Annotation,
    annotations: StringMap<Annotation>,
    blockIndexToAnnotationIds: StringMap<Set<string>>,
    selectedSsieAnnotations: Annotation[]
) => {
    let newAdjustLabelForOverlap = false;
    const blockDescriptorBlockIndices = getAnnotationBlockIndices([annotation]);
    const checkedAnnotationIds = new Set<string>();
    for (const blockIndex of blockDescriptorBlockIndices) {
        // only check to adjust if there are any primary annotation entities across the same blocks
        const annotationIdSet = blockIndexToAnnotationIds[blockIndex];
        if (annotationIdSet) {
            for (const annoId of Array.from(annotationIdSet)) {
                if (!checkedAnnotationIds.has(annoId)) {
                    // only adjust for overlap if primary annotation entity is part of a discontiguous entity or is in a staged relation
                    const anno = annotations[annoId];
                    if (anno.relationId || (anno.inStagedRelation && selectedSsieAnnotations.length > 1)) {
                        newAdjustLabelForOverlap = true;
                        break;
                    }
                    checkedAnnotationIds.add(annoId);
                }
            }
            if (newAdjustLabelForOverlap) {
                break;
            }
        }
    }
    return newAdjustLabelForOverlap;
}

export default ConnectionLabel;
