import React from 'react';
import { Button, CardHeader, Collapse } from 'reactstrap';
import { Annotation, SsieAnnotationStore } from '../../context/SsieAnnotationStore';
import './SelectedEntitiesSidePanel.css';
import SelectedEntity from './components/SelectedEntity/SelectedEntity';
import { PDFStore } from '../../context/PDFStore';
import { copyAnnotation, getBoundsForBlocks, handleAnnotationSelect } from '../../utils/SsieUtils';
import { BlockDescriptor } from '../../types/BlockDescriptor';

interface Props {}

/**
 * Component for a side panel in the semi-structured view that shows all existing annotations.
 * Screenshot: https://tiny.amazon.com/npd28n9f/drivcorpamazdocudnleCRpng
 */
const SelectedEntitiesSidePanel: React.FC<Props> = () => {
    const { selectedSsieAnnotations, setSsieAnnotationStore, pdfBlocks, ssieAnnotations } = React.useContext(SsieAnnotationStore);
    const { currPdfPage, pdfPage, pageInfos } = React.useContext(PDFStore);

    const [showSelectedEntities, setShowSelectedEntities] = React.useState<boolean>(true);

    const handleUpdateAnnotationText = React.useCallback(
        (annotation: Annotation, textOffsets: [number, number]) => {
            if (pageInfos && pageInfos[currPdfPage - 1]) {
                const pageInfo = pageInfos[currPdfPage - 1];
                const newSelectedSsieAnnotations = selectedSsieAnnotations.map((selAnno) => {
                    const copiedAnno = copyAnnotation(selAnno);
                    if (copiedAnno.id !== annotation.id) {
                        return copiedAnno;
                    } else {
                        // get full text offsets for each block
                        let runningOffset = 0;
                        const adjustedBlockDescriptors: BlockDescriptor[] = [];
                        for (const block of copiedAnno.blockDescriptors) {
                            const blockFullTextOffsets = [runningOffset, pdfBlocks[block.blockIndex].Text.length + runningOffset];
                            // check if block is included
                            const beginningIntersect = blockFullTextOffsets[0] <= textOffsets[0] && blockFullTextOffsets[1] >= textOffsets[0];
                            const endIntersect = blockFullTextOffsets[0] <= textOffsets[1] && blockFullTextOffsets[1] >= textOffsets[1];
                            const middleIntersect = blockFullTextOffsets[0] >= textOffsets[0] && blockFullTextOffsets[1] <= textOffsets[1];
                            if (beginningIntersect || middleIntersect || endIntersect) {
                                if (beginningIntersect) {
                                    block.startOffset = textOffsets[0] - runningOffset;
                                }
                                if (endIntersect) {
                                    block.endOffset = textOffsets[1] - runningOffset;
                                }
                                if (block.startOffset !== block.endOffset) {
                                    adjustedBlockDescriptors.push(block);
                                }
                            }
                            runningOffset += pdfBlocks[block.blockIndex].Text.length + 1;
                        }
                        copiedAnno.blockDescriptors = adjustedBlockDescriptors;
                        copiedAnno.bounds = getBoundsForBlocks(adjustedBlockDescriptors.map((blockDescriptor) => pageInfo.blocks[blockDescriptor.blockIndex]), pageInfo.width, pageInfo.height);
                        return copiedAnno;
                    }
                });
                setSsieAnnotationStore({
                    selectedSsieAnnotations: newSelectedSsieAnnotations,
                    ssieAnnotations: ssieAnnotations.updateAnnotations(newSelectedSsieAnnotations)
                });
            }
        },
        [setSsieAnnotationStore, selectedSsieAnnotations, pdfBlocks, pageInfos, currPdfPage]
    );

    /**
     * Makes copies of annotations (does not copy their connections) and selects them.
     */
    const handleCopyAnnotations = React.useCallback(
        (annotation?: Annotation) => {
            const copiedAnnotations = (annotation ? [annotation] : selectedSsieAnnotations).map((anno) => copyAnnotation(anno, true));
            setSsieAnnotationStore({
                ssieAnnotations: ssieAnnotations.updateAnnotations(copiedAnnotations),
                selectedSsieAnnotations: [...selectedSsieAnnotations, ...copiedAnnotations],
            });
        },
        [selectedSsieAnnotations, ssieAnnotations, setSsieAnnotationStore]
    );

    /**
     * Removes annotations and their connections.
     */
    const handleRemoveAnnotations = React.useCallback(
        (annotation?: Annotation) => {
            setSsieAnnotationStore({
                ssieAnnotations: ssieAnnotations.deleteAnnotations(annotation ? [annotation] : selectedSsieAnnotations),
                selectedSsieAnnotations: annotation ? selectedSsieAnnotations.filter((anno) => anno.id !== annotation.id) : [],
                stagedRelation: [],
            });
        },
        [selectedSsieAnnotations, ssieAnnotations, setSsieAnnotationStore]
    );

    const handleSelectClick = React.useCallback(
        (annotation: Annotation, overlap?: boolean) => {
            handleAnnotationSelect({ setSsieAnnotationStore, ssieAnnotations, annotation, selectOverlap: !!overlap, fromControls: true });
        },
        [setSsieAnnotationStore, ssieAnnotations]
    );

    return currPdfPage === parseInt(pdfPage) && selectedSsieAnnotations.length > 0 ? (
        <div className="card" id="selected-entities-side-panel__container">
            <CardHeader>
                <Button className="TagButton" color="success" onClick={() => setShowSelectedEntities((s) => !s)}>
                    SELECTED ENTITIES
                </Button>
                <span>
                    <span id="selected-entities-side-panel__copy" onClick={() => handleCopyAnnotations()} title="Make copy of all selected entities">
                        COPY
                    </span>
                    <span
                        id="selected-entities-side-panel__remove"
                        onClick={() => handleRemoveAnnotations()}
                        title="Remove all selected entities (and deletes their connection if it exists)"
                    >
                        REMOVE
                    </span>
                </span>
            </CardHeader>
            <Collapse isOpen={showSelectedEntities}>
                <div className="card-body d-flex flex-column selected-entities-side-panel__list-container">
                    {selectedSsieAnnotations.map((anno) => {
                        const fullText: string[] = [];
                        const textOffsets: [number, number] = [0, 0];
                        let runningOffset = 0;
                        for (let i = 0; i < anno.blockDescriptors.length; i++) {
                            const blockText = pdfBlocks[anno.blockDescriptors[i].blockIndex].Text;
                            if (i === 0) {
                                textOffsets[0] = anno.blockDescriptors[i].startOffset;
                            }
                            if (i !== anno.blockDescriptors.length - 1) {
                                runningOffset += 1;
                            }
                            runningOffset += anno.blockDescriptors[i].endOffset;
                            fullText.push(blockText);
                        }
                        textOffsets[1] = runningOffset;
                        return (
                            <SelectedEntity
                                onUpdateAnnotationText={(textOffsets: [number, number]) => handleUpdateAnnotationText(anno, textOffsets)}
                                fullText={fullText.join(' ')}
                                textOffsets={textOffsets}
                                key={anno.id}
                                tag={anno.tag}
                                onCopyClick={() => handleCopyAnnotations(anno)}
                                onSelectClick={(overlap?: boolean) => handleSelectClick(anno, overlap)}
                                onRemoveClick={() => handleRemoveAnnotations(anno)}
                                relationId={anno.relationId}
                            />
                        );
                    })}
                </div>
            </Collapse>
        </div>
    ) : null;
};

export default SelectedEntitiesSidePanel;
