import React from 'react';
import { Annotation, defaultSsieAnnotationStore, SsieAnnotations, SsieAnnotationStore } from './SsieAnnotationStore';
import useUndo from 'use-undo';
import { PDFStore } from './PDFStore';
import { UNASSIGNED_TAG } from '../../../store/TaggingStateStore';
import { Block } from '../types/Block';
import { EMPTY_SSIE_OUTPUT_OBJECT, SsieOutputObject } from '../types/SsieOutputObject';
import { DEFAULT_METADATA, PDF_BLOCKS, PRIMARY_ANNOTATION, SECONDARY_ANNOTATION } from '../utils/DefaultSsieCollection';
import { BLOCK_TYPE, DOCUMENT_TYPE, flattenArray, addBlockIndices, getUpdatedStagedRelationAnnotations } from '../utils/SsieUtils';
import { HotKeys } from 'react-hotkeys';
import { StringMap } from '../types/StringMap';
import { getSsieAnnotationsFromEntities, ssieAnnotationsToOutputObject } from '../utils/SsieImportExportUtils'
import {PropertyDataStore} from "../../../store/PropertyDataStore";
import { Namespaces } from '../../../core/Namespaces';
import PDFPageInfo from '../types/PDFPageInfo';

/**
 * Props passed into the semi-structured store
 * @param pdfBlocks list of pdf blocks retrieved from the PDF page or an ingested annotation json file
 * @param setOutputValueCallback callback function set to return annotation data, used by Sagemaker GroundTruth
 * @param pdfType document type
 * @param pdfVersion version of the semi-structured annoation tool
 * @param pdfBlocksS3Ref S3 reference to PDF blocks
 * @param metadata contains key-value information about the PDF page
 * @param annotatorMetadata contains key-value information for the annotator (can be set from the ComprehendSSIEAnnotationTool CLI script argument)
 * @param primaryAnnotation primary annotation json file data
 * @param secondaryAnnotation secondary annotation json file data
 * @param rootStore raw text root store
 */
interface SsieAnnotationStoreProviderProps {
    pdfBlocks?: Block[];
    setOutputValueCallback?: any;
    pdfType?: DOCUMENT_TYPE;
    pdfVersion?: string;
    pdfBlocksS3Ref?: string;
    metadata?: StringMap<any>;
    annotatorMetadata?: StringMap<any>;
    primaryAnnotation?: SsieOutputObject;
    secondaryAnnotation?: SsieOutputObject;
    rootStore?: any;
}

/**
 * Top-level store for semi-structured annotation related data which any child component can retrieve from.
 * This component contains multiple functions which:
 *  - initializes default store data
 *  - creates functions that can be used to alter store data.
 *  - create annotations using the AUTO TAG feature
 *  - packs and transforms annotation data into a callback function to be used by Sagemaker GroundTruth
 *  - create hotkeys to be used by the HotKeyProvider
 */
const SsieAnnotationStoreProvider: React.FC<SsieAnnotationStoreProviderProps> = ({
    pdfBlocks: pdfBlocksProp,
    setOutputValueCallback,
    pdfType: pdfTypeProp,
    pdfVersion,
    pdfBlocksS3Ref: pdfBlocksS3RefProp,
    metadata: metadataProp,
    annotatorMetadata: annotatorMetadataProp,
    primaryAnnotation: primaryAnnotationProp,
    secondaryAnnotation: secondaryAnnotationProp,
    rootStore,
    children,
}) => {
    const [state, { set, undo: undoSsieAction }] = useUndo<{
        ssieAnnotations: SsieAnnotations;
        selectedSsieAnnotations: Annotation[];
        stagedRelation: string[];
        secondarySsieAnnotations: SsieAnnotations;
        selectedSecondaryAnnotations: Annotation[];
    }>(defaultSsieAnnotationStore);
    const [pdfType, setPdfType] = React.useState<DOCUMENT_TYPE>(DOCUMENT_TYPE.NativePDF);
    const [version, setVersion] = React.useState<string>('');
    const [pdfBlocksS3Ref, setPdfBlocksS3Ref] = React.useState<string>('');
    const [showEntityLabels, setShowEntityLabels] = React.useState<boolean>(true);
    const [showEntityConnections, setShowEntityConnections] = React.useState<boolean>(true);
    const [showSecondaryAnnotations, setShowSecondaryAnnotations] = React.useState<boolean>(true);
    const [showAnnotationBoundingBoxes, setShowAnnotationBoundingBoxes] = React.useState<boolean>(true);
    const [metadata, setMetadata] = React.useState<StringMap<any>>(DEFAULT_METADATA);
    const [annotatorMetadata, setAnnotatorMetadata] = React.useState<StringMap<any>>({});
    const [propertyDataStore, setPropertyDataStore] = React.useState<PropertyDataStore>(new PropertyDataStore({
        tags: {},
        document: []
    }));
    const [pdfBlocks, setPdfBlocks] = React.useState<Block[]>([]);
    const [allPdfBlocks, setAllPdfBlocks] = React.useState<Block[]>([]);

    const [selectedTag, setSelectedTag] = React.useState<string | undefined>(UNASSIGNED_TAG);

    const [inputAnnotationSet, setInputAnnotationSet] = React.useState<boolean>(false);

    const { pageInfos, currPdfPage } = React.useContext(PDFStore);

    const { ssieAnnotations, selectedSsieAnnotations, stagedRelation, secondarySsieAnnotations, selectedSecondaryAnnotations } = state.present;

    React.useEffect(() => {
        if (metadataProp) {
            setMetadata(metadataProp);
        }
    }, [metadataProp]);

    React.useEffect(() => {
        if (annotatorMetadataProp) {
            setAnnotatorMetadata(annotatorMetadataProp);
        }
    }, [annotatorMetadataProp]);

    React.useEffect(() => {
        if (pdfBlocksS3RefProp) {
            setPdfBlocksS3Ref(pdfBlocksS3RefProp);
        }
    }, [pdfBlocksS3RefProp]);

    React.useEffect(() => {
        if (pdfTypeProp) {
            setPdfType(pdfTypeProp);
        }
    }, [pdfTypeProp]);

    React.useEffect(() => {
        if (pdfVersion) {
            setVersion(pdfVersion);
        }
    }, [pdfVersion]);

    React.useEffect(() => {
        if (pdfBlocksProp) {
            const correctedPdfBlocksProp = addBlockIndices(pdfBlocksProp);
            setPdfBlocks(correctedPdfBlocksProp.filter((block) => block.BlockType === BLOCK_TYPE.WORD));
            setAllPdfBlocks(correctedPdfBlocksProp);
        } else {
            const wordBlocks = addBlockIndices(PDF_BLOCKS).filter((block) => block.BlockType === BLOCK_TYPE.WORD);
            setPdfBlocks(wordBlocks);
            setAllPdfBlocks(addBlockIndices(PDF_BLOCKS));
        }
    }, [pdfBlocksProp]);

    React.useEffect(() => {
        if (pageInfos && !inputAnnotationSet) {
            initializeSsieAnnotationStore(
                primaryAnnotationProp,
                selectedSsieAnnotations,
                stagedRelation,
                secondaryAnnotationProp,
                selectedSecondaryAnnotations,
                pageInfos,
                currPdfPage,
                pdfBlocks,
                pdfBlocksProp,
                setInputAnnotationSet,
                set,
                propertyDataStore
            );
        }
    }, [
        primaryAnnotationProp,
        secondaryAnnotationProp,
        pageInfos,
        currPdfPage,
        pdfBlocks,
        inputAnnotationSet,
        selectedSsieAnnotations,
        stagedRelation,
        pdfBlocksProp,
        selectedSecondaryAnnotations,
        set,
        setInputAnnotationSet
    ]);

    const getOutputObject = React.useCallback((): SsieOutputObject => {
        return ssieAnnotationsToOutputObject(pdfBlocks, allPdfBlocks, pdfBlocksS3Ref, ssieAnnotations, propertyDataStore, pdfType, version, metadata);
    }, [pdfBlocks, allPdfBlocks, ssieAnnotations, pdfType, version, pdfBlocksS3Ref, metadata]);

    React.useEffect(() => {
        if (rootStore && rootStore.editorStateStore && rootStore.editorStateStore.currentTask && rootStore.editorStateStore.currentTask.namespace === Namespaces.SSIE &&
            setOutputValueCallback) {
            setOutputValueCallback(getOutputObject);
        }
    }, [setOutputValueCallback, getOutputObject, rootStore]);

    const autoTagSsieToken = React.useCallback(() => {
        if (pageInfos && selectedSsieAnnotations.length > 0) {
            let newAnnotations: Annotation[] = [];

            const replacedAnnotationIdsToDelete: string[] = [];
            for (const selAnno of selectedSsieAnnotations) {
                const pageInfo = pageInfos[selAnno.page - 1];

                const annotatedBlockIndices = getAnnotatedBlockIndices(ssieAnnotations, selAnno);

                const searchBlocks = selAnno.blockDescriptors.map(({ blockIndex }) => pageInfo.blocks[blockIndex]);
                const startBlockIndices = hasBlockSubArray({
                    allBlocks: pageInfo.blocks,
                    searchBlocks,
                    ignoreBlockIndices: annotatedBlockIndices,
                });

                for (const startBlockIndex of startBlockIndices) {
                    if (ssieAnnotations.blockIndexToAnnotationIds[startBlockIndex]) {
                        replacedAnnotationIdsToDelete.push(...Array.from(ssieAnnotations.blockIndexToAnnotationIds[startBlockIndex]));
                    }

                    if (startBlockIndex > -1) {
                        const partialBlockDescriptor: { index: number }[] = [];
                        for (let i = startBlockIndex; i < startBlockIndex + searchBlocks.length; i++) {
                            partialBlockDescriptor.push({ index: i });
                        }
                        const newAnno = pageInfo.createAnnotationFromPartialBlockDescriptor(partialBlockDescriptor, selAnno.tag, pdfBlocks)!;
                        addBlockDescriptorOffsets(newAnno, selAnno);
                        newAnnotations.push(newAnno);
                    }
                }
            }

            const replacedAnnotations: Annotation[] = replacedAnnotationIdsToDelete.map(annotationIdToDlete => ssieAnnotations.annotations[annotationIdToDlete]);
            set({
                ...state.present,
                ssieAnnotations: ssieAnnotations.deleteAnnotations(replacedAnnotations).updateAnnotations(newAnnotations),
            });
        }
    }, [selectedSsieAnnotations, ssieAnnotations, state, pageInfos, set, pdfBlocks]);

    const handlers = {
        TOGGLE_ENTITY_LABELS: (event: any) => {
            setShowEntityLabels((l) => !l);
        },
        TOGGLE_SPAN_CONNECTIONS: (event: any) => {
            setShowEntityConnections((r) => !r);
        },
        TOGGLE_SECONDARY_ANNOTATIONS: (event: any) => {
            setShowSecondaryAnnotations((s) => !s);
        },
        TOGGLE_ANNOTATION_BOUNDING_BOXES: (event: any) => {
            setShowAnnotationBoundingBoxes((s) => (!s));
        }
    };

    return (
        <SsieAnnotationStore.Provider
            value={{
                ssieAnnotations,
                secondarySsieAnnotations,
                selectedSsieAnnotations,
                selectedSecondaryAnnotations,
                setSsieAnnotationStore: ({ ssieAnnotations, selectedSsieAnnotations, stagedRelation, selectedSecondaryAnnotations }) => {
                    if (selectedSsieAnnotations && selectedSsieAnnotations.length > 0 && selectedSsieAnnotations[0].tag === UNASSIGNED_TAG) {
                        setSelectedTag(undefined);
                    }

                    const useSsieAnnotations = ssieAnnotations ? ssieAnnotations : state.present.ssieAnnotations;
                    const updateInStagedRelationAnnotations: Annotation[] = getUpdatedStagedRelationAnnotations(
                        state.present.stagedRelation,
                        ssieAnnotations ? ssieAnnotations : state.present.ssieAnnotations,
                        stagedRelation ? stagedRelation : []
                    );

                    set({
                        ...state.present,
                        ssieAnnotations: useSsieAnnotations.updateAnnotations(updateInStagedRelationAnnotations),
                        selectedSsieAnnotations: selectedSsieAnnotations ? selectedSsieAnnotations : state.present.selectedSsieAnnotations,
                        stagedRelation: stagedRelation ? stagedRelation : state.present.stagedRelation,
                        selectedSecondaryAnnotations: selectedSecondaryAnnotations ? selectedSecondaryAnnotations : [],
                    });
                },
                undoSsieAction,
                autoTagSsieToken,
                stagedRelation,
                selectedTag,
                setSelectedTag,
                getOutputObject,
                pdfBlocks,
                showEntityLabels,
                setShowEntityLabels,
                showEntityConnections,
                setShowEntityConnections,
                metadata,
                annotatorMetadata,
                showSecondaryAnnotations,
                setShowSecondaryAnnotations,
                setPropertyDataStore,
                propertyDataStore,
                showAnnotationBoundingBoxes,
                setShowAnnotationBoundingBoxes
            }}
        >
            <HotKeys handlers={handlers}>{children}</HotKeys>
        </SsieAnnotationStore.Provider>
    );
};

/**
 * Copies block descriptor offsets from one annotation to another
 * @param annotation Annotation to copy block descriptor offsets TO
 * @param copyAnnotation Annotation to copy block descriptor offsets FROM
 */
export const addBlockDescriptorOffsets = (annotation: Annotation, copyAnnotation: Annotation) => {
    for (let i = 0; i < copyAnnotation.blockDescriptors.length; i++) {
        annotation.blockDescriptors[i].startOffset = copyAnnotation.blockDescriptors[i].startOffset;
        annotation.blockDescriptors[i].endOffset = copyAnnotation.blockDescriptors[i].endOffset;
    }
};

export const prepareAsFile = (s: SsieOutputObject) => {
    return [JSON.stringify(s)];
};

export function hasBlockSubArray({
    allBlocks,
    searchBlocks,
    ignoreBlockIndices,
}: {
    allBlocks: Block[];
    searchBlocks: Block[];
    ignoreBlockIndices?: Set<number>;
}): number[] {
    const startBlockIndices: number[] = [];
    if (allBlocks.length >= searchBlocks.length) {
        let i = 0;
        for (; i < allBlocks.length - searchBlocks.length + 1; i++) {
            if (!ignoreBlockIndices || !ignoreBlockIndices.has(i)) {
                let j = 0;
                for (; j < searchBlocks.length; j++) {
                    if (allBlocks[i + j].Text.toLowerCase() !== searchBlocks[j].Text.toLowerCase()) {
                        break;
                    }
                }
                if (j === searchBlocks.length) {
                    startBlockIndices.push(i);
                    i += searchBlocks.length;
                    continue;
                }
            }
        }
    }
    return startBlockIndices;
}

export const initializeSsieAnnotationStore = (
    primaryAnnotationProp: SsieOutputObject | undefined,
    selectedSsieAnnotations: Annotation[],
    stagedRelation: string[],
    secondaryAnnotationProp: SsieOutputObject | undefined,
    selectedSecondaryAnnotations: Annotation[],
    pdfPageInfos: PDFPageInfo[],
    currPdfPage: number,
    pdfBlocks: Block[],
    pdfBlocksProp: Block[] | undefined,
    setInputAnnotationSet: (inputAnnotationSet: boolean) => void,
    set: (presentStore: any) => void,
    propertyDataStore: PropertyDataStore,
) => {
    const page = pdfPageInfos[currPdfPage - 1];
    // check if PDF blocks are loaded
    if (page.blocks.length) {
        const primaryAnnotationDynamic = !!primaryAnnotationProp
            ? primaryAnnotationProp
            : pdfBlocksProp
            ? EMPTY_SSIE_OUTPUT_OBJECT
            : PRIMARY_ANNOTATION;
        const ssieAnnotationsToSet = getSsieAnnotationsFromEntities(primaryAnnotationDynamic, page, pdfBlocks, propertyDataStore);
        const secondaryAnnotationDynamic = !!secondaryAnnotationProp
            ? secondaryAnnotationProp
            : pdfBlocksProp
            ? EMPTY_SSIE_OUTPUT_OBJECT
            : SECONDARY_ANNOTATION;
        const secondarySsieAnnotationsToSet = getSsieAnnotationsFromEntities(secondaryAnnotationDynamic, page, pdfBlocks, propertyDataStore);
        set({
            ssieAnnotations: ssieAnnotationsToSet,
            selectedSsieAnnotations,
            stagedRelation,
            secondarySsieAnnotations: secondarySsieAnnotationsToSet,
            selectedSecondaryAnnotations,
        });
        setInputAnnotationSet(true);
    }
}

/**
 * Get block indices for all annotations
 * @param ssieAnnotations 
 * @param selectedAnnotation
 */
export const getAnnotatedBlockIndices = (
    ssieAnnotations: SsieAnnotations,
    selectedAnnotation: Annotation
) => {
    // don't search for spans in any existing annotations unless they are unassigned
    const blockIndicesArrays = Object.values(ssieAnnotations.annotations)
        .filter((anno) =>
            anno.tag !== UNASSIGNED_TAG || anno.id === selectedAnnotation.id ||
            anno.blockDescriptors.length !== selectedAnnotation.blockDescriptors.length)
        .map((anno) => anno.blockDescriptors.map((t) => t.blockIndex));
    return new Set<number>(flattenArray(blockIndicesArrays));
}

export default SsieAnnotationStoreProvider;
