/**
 * Block object with offsets that can be adjusted
 * @param pageIndex Page index (starts at 0)
 * @param blockIndex Block index in pdf WORD block list
 * @param blockType BLOCK_TYPE
 * @param startOffset default value of 0 (can be adjusted)
 * @param endOffset default value of length of block word (can be adjusted)
 */
export class BlockDescriptor {
    public id: string;
    constructor(
        public readonly pageIndex: number,
        public readonly blockIndex: number,
        public readonly blockType: string,

        // start and end offsets can be altered during text-subselection which occurs in the SelectedEntity component
        public startOffset: number, //default value of 0
        public endOffset: number // default value of length of block word
    ) {
        this.id = [this.pageIndex.toString(), this.blockIndex.toString()].join('-');
    }
}
