import { BlockDescriptor } from '../types/BlockDescriptor';
import {PropertyDataStore} from "../../../store/PropertyDataStore";
import { cloneDeep } from 'lodash';

export const areSameTag = (annotations: Annotation[]) => {
    return new Set(annotations.map((anno) => anno.tag)).size === 1;
};

export const getEntityConnectName = (relationId: string) => {
    return `CONN_${relationId.substring(0, 3)}`;
};

export class Annotation {
    public readonly id: string;
    public relationId: string | null;
    public inStagedRelation: boolean;

    constructor(
        public bounds: Bounds,
        public readonly page: number,
        public readonly tag: string,
        public blockDescriptors: BlockDescriptor[] = [],
        id: string | undefined = undefined
    ) {
        this.id = id ? id : uuidv4();
        this.relationId = null;
        this.inStagedRelation = false;
    }

    toString() {
        return this.id;
    }
}

export class DiscontiguousRelation {
    public id: string;

    constructor(
        public annotationIds: string[]
    ) {
        this.id = uuidv4();
    }
}

export class SsieAnnotations {
    /**
     * SsieAnnotations constructor
     * @param annotations map of annotation id to annotations for quick annotation lookup
     * @param discontiguousRelations map of discontiguous relation id to discontiguous relations for quick discontiguous relation lookup
     * @param blockIndexToAnnotationIds map of block indices to annotation id sets for processing which blocks contain primary and secondary annotations
     * @param annotationBlockDescriptorIds set of block descriptor ids for existing annotations
     */
    constructor(public readonly annotations: StringMap<Annotation>, public readonly discontiguousRelations: StringMap<DiscontiguousRelation>,
        public readonly blockIndexToAnnotationIds: StringMap<Set<string>>, public readonly annotationBlockDescriptorIds: Set<string>) {}

    updateAnnotations(updatedAnnotations: Annotation[]): SsieAnnotations {
        const newAnnotationsMap: StringMap<Annotation> = cloneDeep(this.annotations);
        const newBlockIndexToPrimaryAnnotationIds: StringMap<Set<string>> = cloneDeep(this.blockIndexToAnnotationIds);
        const newAnnotationBlockDescriptorIds: Set<string> = new Set(this.annotationBlockDescriptorIds);
        
        for (const updateAnnotation of updatedAnnotations) {
            const oldAnnotation = newAnnotationsMap[updateAnnotation.id];
            if (oldAnnotation) {
                for (const blockDescriptor of oldAnnotation.blockDescriptors) {
                    newBlockIndexToPrimaryAnnotationIds[blockDescriptor.blockIndex].delete(oldAnnotation.id);
                    newAnnotationBlockDescriptorIds.delete(blockDescriptor.id);
                }
            }
            for (const blockDescriptor of updateAnnotation.blockDescriptors) {
                if (!newBlockIndexToPrimaryAnnotationIds[blockDescriptor.blockIndex]) {
                    newBlockIndexToPrimaryAnnotationIds[blockDescriptor.blockIndex] = new Set<string>();
                }
                newBlockIndexToPrimaryAnnotationIds[blockDescriptor.blockIndex].add(updateAnnotation.id);
                newAnnotationBlockDescriptorIds.add(blockDescriptor.id);
            }
            
            newAnnotationsMap[updateAnnotation.id] = updateAnnotation;
        }

        return new SsieAnnotations(newAnnotationsMap, this.discontiguousRelations, newBlockIndexToPrimaryAnnotationIds, newAnnotationBlockDescriptorIds);
    }

    deleteAnnotations(deleteAnnotations: Annotation[]): SsieAnnotations {
        const newAnnotationsMap: StringMap<Annotation> = cloneDeep(this.annotations);
        const newRelationsMap: StringMap<DiscontiguousRelation> = cloneDeep(this.discontiguousRelations);
        const newBlockIndexToPrimaryAnnotationIds: StringMap<Set<string>> = cloneDeep(this.blockIndexToAnnotationIds);
        const newAnnotationBlockDescriptorIds: Set<string> = new Set(this.annotationBlockDescriptorIds);

        for (const deleteAnnotation of deleteAnnotations) {
            if (deleteAnnotation.relationId) {
                const discontiguousRelation = newRelationsMap[deleteAnnotation.relationId];
                if (discontiguousRelation) { // discontiguous relation id may have already been removed in a previous loop iteration
                    for (const annotationId of discontiguousRelation.annotationIds) {
                        newAnnotationsMap[annotationId].relationId = null;
                    }
                    delete newRelationsMap[deleteAnnotation.relationId];
                }
            }

            for (const blockDescriptor of deleteAnnotation.blockDescriptors) {
                newBlockIndexToPrimaryAnnotationIds[blockDescriptor.blockIndex].delete(deleteAnnotation.id);
                newAnnotationBlockDescriptorIds.delete(blockDescriptor.id);
            }

            delete newAnnotationsMap[deleteAnnotation.id];
        }
        return new SsieAnnotations(newAnnotationsMap, newRelationsMap, newBlockIndexToPrimaryAnnotationIds, newAnnotationBlockDescriptorIds);
    }

    /**
     * Add a new relation to the annotation store
     * @param relationAnnotationIds list of annotation ID's in a relation
     */
    withNewRelation(relationAnnotationIds: string[]): SsieAnnotations {
        const newAnnotationsMap: StringMap<Annotation> = cloneDeep(this.annotations);
        const newRelationsMap: StringMap<DiscontiguousRelation> = cloneDeep(this.discontiguousRelations);

        if (relationAnnotationIds.length > 1) {
            const newRelation = new DiscontiguousRelation(relationAnnotationIds);
            for (const relationAnnotationId of relationAnnotationIds) {
                newAnnotationsMap[relationAnnotationId].relationId = newRelation.id;
            }
            newRelationsMap[newRelation.id] = newRelation;
            return new SsieAnnotations(newAnnotationsMap, newRelationsMap, this.blockIndexToAnnotationIds, this.annotationBlockDescriptorIds);
        }
        return this;
    }

    /**
     * Delete relations from the annotation store
     * @param discontiguousRelationIds list of discontiguous relation id's
     * @returns
     */
    deleteRelations(discontiguousRelationIds: string[]): SsieAnnotations {
        const newAnnotationsMap: StringMap<Annotation> = cloneDeep(this.annotations);
        const newRelationsMap: StringMap<DiscontiguousRelation> = cloneDeep(this.discontiguousRelations);

        for (const discontiguousRelationId of discontiguousRelationIds) {
            const discontiguousRelation = newRelationsMap[discontiguousRelationId];
            if (discontiguousRelation) { // discontiguous relation id may have already been removed in a previous loop iteration
                for (const annotationId of discontiguousRelation.annotationIds) {
                    newAnnotationsMap[annotationId].relationId = null;
                }
                delete newRelationsMap[discontiguousRelationId];
            }
        }
        return new SsieAnnotations(newAnnotationsMap, newRelationsMap, this.blockIndexToAnnotationIds, this.annotationBlockDescriptorIds);
    }
}

export interface SsieAnnotationStore {
    pdfBlocks: Block[];

    // holds annotation IDs which can be changed to a discontiguous relation
    stagedRelation: string[];

    ssieAnnotations: SsieAnnotations;
    secondarySsieAnnotations: SsieAnnotations;

    selectedSsieAnnotations: Annotation[];

    selectedSecondaryAnnotations: Annotation[];

    propertyDataStore?: PropertyDataStore;

    setSsieAnnotationStore: ({
        ssieAnnotations,
        selectedSsieAnnotations,
        hideSsieAnnotationTags,
        stagedRelation,
        selectedSecondaryAnnotations,
    }: {
        ssieAnnotations?: SsieAnnotations;
        selectedSsieAnnotations?: Annotation[];
        hideSsieAnnotationTags?: boolean;
        stagedRelation?: string[];
        selectedSecondaryAnnotations?: Annotation[];
    }) => void;

    setPropertyDataStore: (store: PropertyDataStore) => void

    autoTagSsieToken: () => void;

    undoSsieAction: () => void;

    selectedTag: string | undefined;
    setSelectedTag: (tag: string | undefined) => void;

    getOutputObject: () => void;

    showEntityLabels: boolean;
    setShowEntityLabels: (l: boolean) => void;
    showEntityConnections: boolean;
    setShowEntityConnections: (r: boolean) => void;

    metadata: StringMap<any>;
    annotatorMetadata: StringMap<any>;

    showSecondaryAnnotations: boolean;
    setShowSecondaryAnnotations: (s: boolean) => void;

    showAnnotationBoundingBoxes: boolean;
    setShowAnnotationBoundingBoxes: (s: boolean) => void;
}

