import React from 'react';
import { Namespaces } from '../../../../core/Namespaces';
import { ContextMenuSelector } from '../../../../menu/ContextMenuSelector';
import { TAGGING_ANNOTATOR_MENU_ID } from '../../../TaggingAnnotator';

import { Annotation, SsieAnnotationStore, SsieAnnotations } from '../../context/SsieAnnotationStore';
import { copyAnnotation, getMultipleAnnotationText, showConnectButton } from '../../utils/SsieUtils';

interface Props {
    taggingStateStore: any;
    updateSelectedSsieAnnotationsProperties: (
        tag: string,
        {
            selectedSsieAnnotations,
            ssieAnnotations,
            setSsieAnnotationStore,
        }: { selectedSsieAnnotations: Annotation[]; ssieAnnotations: SsieAnnotations; setSsieAnnotationStore: any }
    ) => void;
    removeActiveSsieAnnotations: (selectedSsieAnnotations: Annotation[], ssieAnnotations: SsieAnnotations, setSsieAnnotationStore: any) => void;
    handleAutoTagClick: (autoTagSsieToken: () => void) => void;
    handleConnectButtonClick: (
        selectedSsieAnnotations: Annotation[],
        ssieAnnotations: SsieAnnotations,
        stagedRelation: string[]
    ) => Partial<SsieAnnotationStore>;
}

/**
 * Component for semi-structured annotation context menu. Uses ContextMenuSelector component from tagging workflow.
 * Screenshot: https://tiny.amazon.com/17a09rwee/drivcorpamazdocudnleCRpng
 */
const SemiStructuredContextMenuSelector: React.FC<Props> = ({
    taggingStateStore,
    updateSelectedSsieAnnotationsProperties,
    removeActiveSsieAnnotations,
    handleAutoTagClick,
    handleConnectButtonClick,
}) => {
    const {
        pdfBlocks,
        selectedSsieAnnotations,
        ssieAnnotations,
        setSsieAnnotationStore,
        stagedRelation,
        autoTagSsieToken,
        selectedSecondaryAnnotations,
        secondarySsieAnnotations,
    } = React.useContext(SsieAnnotationStore);

    const [secondaryAnnotationClicked, setSecondaryAnnotationClicked] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (taggingStateStore.schemaKey === Namespaces.SSIE && selectedSecondaryAnnotations.length > 0) {
            setSecondaryAnnotationClicked(true);
        } else {
            setSecondaryAnnotationClicked(false);
        }
    }, [taggingStateStore, selectedSecondaryAnnotations]);

    const handleReplacePrimaryAnnotation = React.useCallback(() => {
        const relationsAnnotationIdsToAdd = selectedSecondaryAnnotations[0].relationId ?
            secondarySsieAnnotations.discontiguousRelations[selectedSecondaryAnnotations[0].relationId].annotationIds : [];
        const annotationsToAdd = selectedSecondaryAnnotations.map((anno) => copyAnnotation(anno));

        const relationsIdsToRemove: string[] = [];        
        for (const annotation of selectedSsieAnnotations) {
            if (annotation.relationId) {
                relationsIdsToRemove.push(annotation.relationId);
            }
        }

        setSsieAnnotationStore({
            selectedSsieAnnotations: [],
            ssieAnnotations: ssieAnnotations
                .deleteRelations(relationsIdsToRemove)
                .deleteAnnotations(selectedSsieAnnotations)
                .updateAnnotations(annotationsToAdd)
                .withNewRelation(relationsAnnotationIdsToAdd),
            stagedRelation: [],
            selectedSecondaryAnnotations: undefined,
        });
    }, [selectedSsieAnnotations, selectedSecondaryAnnotations, setSsieAnnotationStore, secondarySsieAnnotations.discontiguousRelations]);

    const getReplacePrimaryAnnotationText = React.useCallback(() => {
        const replaceFromText = getMultipleAnnotationText(selectedSsieAnnotations, pdfBlocks);
        const replaceToText = getMultipleAnnotationText(selectedSecondaryAnnotations, pdfBlocks);;
        return 'REPLACE "' + replaceFromText.join(' | ') + '" with "' + replaceToText.join(' | ') + '"';
    }, [selectedSsieAnnotations, selectedSecondaryAnnotations, pdfBlocks]);

    return (
        <ContextMenuSelector
            menuId={TAGGING_ANNOTATOR_MENU_ID}
            numItem={4}
            tags={taggingStateStore.tagGroupStore.tags}
            items={taggingStateStore.tagGroupStore.tags}
            clickHandler={(tag: string) => {
                taggingStateStore.updateActiveTag(tag);
                updateSelectedSsieAnnotationsProperties(tag, {
                    selectedSsieAnnotations,
                    ssieAnnotations,
                    setSsieAnnotationStore,
                });
            }}
            untag={{
                untagHandler: () => {
                    taggingStateStore.removeActiveSpan();
                    removeActiveSsieAnnotations(selectedSsieAnnotations, ssieAnnotations, setSsieAnnotationStore);
                },
                untagText: 'REMOVE',
            }}
            autoTag={{
                autoTagHandler: () => handleAutoTagClick(autoTagSsieToken),
                autoTagText: 'AUTO TAG',
            }}
            connect={
                showConnectButton(selectedSsieAnnotations, stagedRelation)
                    ? {
                          connectHandler: () =>
                          setSsieAnnotationStore(handleConnectButtonClick(selectedSsieAnnotations, ssieAnnotations, stagedRelation)),
                          connectText: selectedSsieAnnotations[0].relationId ? 'DISCONNECT' : 'CONNECT',
                      }
                    : {}
            }
            replacePrimaryAnnotation={
                secondaryAnnotationClicked
                    ? {
                          replacePrimaryAnnotationHandler: handleReplacePrimaryAnnotation,
                          replacePrimaryAnnotationText: getReplacePrimaryAnnotationText(),
                      }
                    : {}
            }
        />
    );
};

export default SemiStructuredContextMenuSelector;
