import React, { useContext } from 'react';
import './Selection.css';

import { Bounds } from '../../types/Bounds';
import PDFPageInfo from '../../types/PDFPageInfo';
import { BlockDescriptor } from '../../types/BlockDescriptor';
import { Annotation, SsieAnnotations, SsieAnnotationStore } from '../../context/SsieAnnotationStore';
import { ContextMenuTrigger } from 'react-contextmenu';
import { TAGGING_ANNOTATOR_MENU_ID } from '../../../TaggingAnnotator';
import { addDiscontiguousRelationAnnotations, annotationBlockDescriptorsEqual, annotationIsNotUnassigned, getAnnotationBlockIndices, getDiscontiguousRelationAnnotations, getEntityBlockDescriptors, handleAnnotationMultiSelect, handleAnnotationSelect, isRightClick } from '../../utils/SsieUtils';
import { RedSpanColor, TagLabelColor } from '../../../../AnnotatorCommons';
import { SECONDARY_ANNOTATION_BOUNDS_ADJUSTMENT } from '../../utils/DefaultSsieCollection';
import ConnectionLabel from './components/ConnectionLabel/ConnectionLabel';
import { StringMap } from '../../types/StringMap';

interface SelectionProps {
    pageInfo: PDFPageInfo;
    annotation: Annotation;
    showInfo?: boolean;
    color: string;
    isSecondary?: boolean;
    bounds: Bounds;
}

/**
 * Component for showing the boundary, entity-type/tag label, and discontiguous/connection label of an annotation.
 * Screenshot: https://tiny.amazon.com/oo3dgqi3/drivcorpamazdocudnleCRpng
 */
export const Selection: React.FC<SelectionProps> = ({ pageInfo, annotation, showInfo = true, color, isSecondary = false, bounds }) => {
    const {
        setSsieAnnotationStore,
        selectedSsieAnnotations,
        ssieAnnotations,
        stagedRelation,
        showEntityLabels,
        showEntityConnections,
        secondarySsieAnnotations,
        pdfBlocks
    } = useContext(SsieAnnotationStore);

    const [adjustBoundsForOverlap, setAdjustBoundsForOverlap] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (isSecondary) {
            setAdjustBoundsForOverlap(secondaryAnnotationAdjustBoundsForOverlap(annotation, ssieAnnotations.annotations, ssieAnnotations.blockIndexToAnnotationIds));
        } else {
            setAdjustBoundsForOverlap(false);
        }
    }, [isSecondary, annotation, ssieAnnotations.annotations, ssieAnnotations.blockIndexToAnnotationIds, setAdjustBoundsForOverlap]);

    const [hasAnnotationDifference, setHasAnnotationDifference] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (isSecondary) {
            setHasAnnotationDifference(!secondaryAnnotationHasSameBlockDescriptors(annotation, ssieAnnotations, secondarySsieAnnotations));
        } else {
            setHasAnnotationDifference(false);
        }
    }, [annotation, isSecondary, ssieAnnotations, secondarySsieAnnotations, setHasAnnotationDifference]);

    const onMouseDown = React.useCallback(
        (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
            if (isRightClick(e)) {
                return;
            }

            // shift-clicking allows mult-annotation selection if at least one annotation is already selected and annotation is NOT part of a discontigous relation
            if (e.shiftKey && selectedSsieAnnotations.length > 0 && !annotation.relationId) {
                handleAnnotationMultiSelect({ setSsieAnnotationStore, selectedSsieAnnotations, stagedRelation, annotation });
            } else {
                handleAnnotationSelect({
                    setSsieAnnotationStore,
                    ssieAnnotations,
                    annotation,
                });
            }

            e.preventDefault();
            e.stopPropagation();
        },
        [selectedSsieAnnotations, annotation, ssieAnnotations, stagedRelation, setSsieAnnotationStore]
    );

    const handleConnectLabelClick = React.useCallback((e: React.MouseEvent<any, MouseEvent>) => {
        e.stopPropagation();

        if (annotation.relationId && !isSecondary) {
            const newSelectedSsieAnnotations = ssieAnnotations.discontiguousRelations[annotation.relationId].annotationIds.map(annotationId => ssieAnnotations.annotations[annotationId]);
            setSsieAnnotationStore({
                selectedSsieAnnotations: newSelectedSsieAnnotations,
                stagedRelation: []
            });
        }
    }, [setSsieAnnotationStore, ssieAnnotations.annotations, ssieAnnotations.discontiguousRelations, annotation]);

    /**
     * 1. Select secondary annotations to add to primary annotations
     * 2. Select primary annotations (including any discontiguous relation annotations) to remove
     */
    const onSecondaryContextMenuClick = React.useCallback(
        (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
            let newSelectedSecondaryAnnotations = [annotation];
            if (annotation.relationId) { 
                newSelectedSecondaryAnnotations = getDiscontiguousRelationAnnotations(secondarySsieAnnotations, annotation.relationId);
            }

            let selectedSingleSsieAnnotations: Annotation[] = [];
            const annotationBlockIndices = getAnnotationBlockIndices(newSelectedSecondaryAnnotations);
            const addedAnnotationIds = new Set<string>();
            for (const blockIndex of annotationBlockIndices) {
                const annotationIdSet = ssieAnnotations.blockIndexToAnnotationIds[blockIndex];
                if (annotationIdSet) {
                    selectedSingleSsieAnnotations = [...selectedSingleSsieAnnotations, ...Array.from(annotationIdSet)
                        .filter(annoId => !addedAnnotationIds.has(annoId))
                        .map(annoId => {
                            addedAnnotationIds.add(annoId);
                            return ssieAnnotations.annotations[annoId];
                        })];
                }
            }
            
            const newSelectedSsieAnnotations = addDiscontiguousRelationAnnotations(selectedSingleSsieAnnotations, ssieAnnotations);
            setSsieAnnotationStore({
                selectedSsieAnnotations: newSelectedSsieAnnotations,
                stagedRelation: newSelectedSsieAnnotations.length === 1 && annotationIsNotUnassigned(newSelectedSsieAnnotations[0]) ? [newSelectedSsieAnnotations[0].id] : [],
                selectedSecondaryAnnotations: newSelectedSecondaryAnnotations,
            });
        },
        [annotation, setSsieAnnotationStore, secondarySsieAnnotations.discontiguousRelations, secondarySsieAnnotations.annotations, ssieAnnotations]
    );

    const onContextMenuClick = React.useCallback(
        (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
            if (isSecondary) {
                onSecondaryContextMenuClick(e);
                return;
            }

            // if clicked annotation is part of a relation, reset staged relations and select all the relation's annotations
            if (annotation.relationId) {
                // select all annotations in relation
                setSsieAnnotationStore({
                    selectedSsieAnnotations: getDiscontiguousRelationAnnotations(ssieAnnotations, annotation.relationId),
                    stagedRelation: [],
                });
            } else {
                // if clicked annotation isn't currently selected, clear selected annotation and staged relation and add it to both
                // otherwise do nothing
                const annotationIsSelected = selectedSsieAnnotations.find((anno) => anno.id === annotation.id);
                if (!annotationIsSelected) {
                    setSsieAnnotationStore({
                        selectedSsieAnnotations: [annotation],
                        stagedRelation: annotationIsNotUnassigned(annotation) ? [annotation.id] : [],
                    });
                }
            }

            e.preventDefault();
        },
        [annotation, ssieAnnotations.annotations, ssieAnnotations.discontiguousRelations, setSsieAnnotationStore, isSecondary, onSecondaryContextMenuClick, selectedSsieAnnotations]
    );

    return (
        <>
            <ContextMenuTrigger id={TAGGING_ANNOTATOR_MENU_ID}>
                <SelectionBoundary
                    bounds={bounds}
                    color={color}
                    adjustBoundsForOverlap={adjustBoundsForOverlap}
                    hasAnnotationDifference={hasAnnotationDifference}
                >
                    {showInfo ? (
                        <>
                            {showEntityLabels && annotationIsNotUnassigned(annotation) && (
                                <span
                                    className={`selection-boundary-span-tag TagStyle NoSelect`}
                                    onMouseDown={isSecondary ? () => {} : onMouseDown}
                                    onContextMenu={onContextMenuClick}
                                    style={{ color: false ? RedSpanColor : TagLabelColor }}
                                >
                                    {annotation.tag}
                                </span>
                            )}
                            {showEntityConnections && (
                                <ConnectionLabel
                                    isSecondary={isSecondary}
                                    annotation={annotation}
                                    onMouseDown={handleConnectLabelClick}
                                    onContextMenuClick={onContextMenuClick}
                                    hasAnnotationDifference={hasAnnotationDifference}
                                />
                            )}
                        </>
                    ) : null}
                </SelectionBoundary>
                {
                    // NOTE: It's important that the parent element of the tokens
                    // is the PDF canvas, because we need their absolute position
                    // to be relative to that and not another absolute/relatively
                    // positioned element. This is why SelectionTokens are not inside
                    // SelectionBoundary.
                    // NOTE: Secondary annotations (from blind2 arbitration annoations)
                    // do not have highlighted tokens.
                    !isSecondary && (
                        <SelectionTokens
                            key={`selection-tokens-${annotation.id}`}
                            pageInfo={pageInfo}
                            blockDescriptors={annotation.blockDescriptors}
                            color={color}
                            onMouseDown={onMouseDown}
                            onContextMenu={onContextMenuClick}
                        />
                    )
                }
            </ContextMenuTrigger>
        </>
    );
};

/**
 * Decide whether to adjust bounds for overlap for a secondary annotation.
 * Bounds will be adjusted for overlap if:
 *  - there are primary annotations with tags that contain the same blocks as the secondary annotation
 * @param secondaryAnnotation Secondary annotation to check overlap for
 * @param annotations Map of current primary annotations
 * @param blockIndexToAnnotationIds Map of current primary block index to annotation id's
 */
export const secondaryAnnotationAdjustBoundsForOverlap = (
    secondaryAnnotation: Annotation,
    primaryAnnotations: StringMap<Annotation>,
    primaryBlockIndexToAnnotationIds: StringMap<Set<string>>
) => {
    const annotationBlockIndices = getAnnotationBlockIndices([secondaryAnnotation]);
    let primaryAnnotationWithTagExists = false;
    for (const blockIndex of annotationBlockIndices) {
        const annotationIdSet = primaryBlockIndexToAnnotationIds[blockIndex];
        if (annotationIdSet) {
            for (const annoId of Array.from(annotationIdSet)) {
                if (annotationIsNotUnassigned(primaryAnnotations[annoId])) {
                    primaryAnnotationWithTagExists = true;
                }
            }
        }
    }
    return primaryAnnotationWithTagExists;
}

/**
 * Check if the secondary annotation's corresponding entity has the same block descriptors as the overlapping primary entities
 * 1. Get the secondary entity's block descriptors
 * 2. Find any overlapping primary annotations
 * 3. From the overlapping primary annotations, check that there is at least one primary entity which has the same block descriptors and same text
 * @param annotation secondary annotation
 * @param primarySsieAnnotations primary SSIE annotations
 * @param secondarySsieAnnotations secondary SSIE annotations
 * @returns whether secondary entity block descriptors are the same as an overlapping primary entity block descriptors
 */
export const secondaryAnnotationHasSameBlockDescriptors = (
    secondaryAnnotation: Annotation,
    primarySsieAnnotations: SsieAnnotations,
    secondarySsieAnnotations: SsieAnnotations
) => {
    const secondaryEntityBlockDescriptors = getEntityBlockDescriptors(secondaryAnnotation, secondarySsieAnnotations);

    // find overlapping primary annotation ids, return false if there aren't any
    const secondaryAnnotationBlockIndices = getAnnotationBlockIndices([secondaryAnnotation]);
    const fullPrimaryAnnotationOverlappingIdSet = new Set<string>();
    for (const secondaryBlockIndex of secondaryAnnotationBlockIndices) {
        const primaryAnnotationIdSet = primarySsieAnnotations.blockIndexToAnnotationIds[secondaryBlockIndex];
        if (primaryAnnotationIdSet) {
            Array.from(primaryAnnotationIdSet).forEach(annoId => fullPrimaryAnnotationOverlappingIdSet.add(annoId))
        } else {
            return false;
        }
    }

    // return whether there are no primary entities with equal block descriptors to secondary entity
    return Array.from(fullPrimaryAnnotationOverlappingIdSet)
        .map(annoId => primarySsieAnnotations.annotations[annoId])
        .filter(foundOverlappingPrimaryAnnotation => foundOverlappingPrimaryAnnotation.tag === secondaryAnnotation.tag)
        .filter(foundOverlappingPrimaryAnnotation => {
            const primaryEntityBlockDescriptors = getEntityBlockDescriptors(foundOverlappingPrimaryAnnotation, primarySsieAnnotations);
            return annotationBlockDescriptorsEqual(secondaryEntityBlockDescriptors, primaryEntityBlockDescriptors);
        }).length > 0;
}

interface SelectionBoundaryProps {
    bounds: Bounds;
    children?: React.ReactNode;
    annotationId?: string;
    color: string;
    adjustBoundsForOverlap?: boolean;
    hasAnnotationDifference?: boolean;
}

export const SelectionBoundary: React.FC<SelectionBoundaryProps> = ({
    bounds,
    children,
    color,
    adjustBoundsForOverlap = false,
    hasAnnotationDifference = false,
}: SelectionBoundaryProps) => {
    const { showAnnotationBoundingBoxes } = useContext(SsieAnnotationStore);

    const width = bounds.right - bounds.left;
    const height = bounds.bottom - bounds.top + (adjustBoundsForOverlap ? SECONDARY_ANNOTATION_BOUNDS_ADJUSTMENT : 0);
    const rotateY = width < 0 ? -180 : 0;
    const rotateX = height < 0 ? -180 : 0;

    return (
        <span
            className="selection-boundary"
            style={{
                position: 'absolute',
                left: `${bounds.left}px`,
                top: `${bounds.top - (adjustBoundsForOverlap ? SECONDARY_ANNOTATION_BOUNDS_ADJUSTMENT : 0)}px`,
                width: `${Math.abs(width)}px`,
                height: `${Math.abs(height)}px`,
                transform: `rotateY(${rotateY}deg) rotateX(${rotateX}deg)`,
                transformOrigin: 'top left',
                border: showAnnotationBoundingBoxes ? `2px solid ${hasAnnotationDifference ? RedSpanColor : color}` : 'none',
                backgroundColor: 'transparent',
                pointerEvents: 'none',
            }}
        >
            {children ? children : null}
        </span>
    );
};

interface SelectionTokenProps {
    pageInfo: PDFPageInfo;
    blockDescriptors: BlockDescriptor[];
    color: string;
    onMouseDown?: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => void;
    onContextMenu?: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => void;
}

export const SelectionTokens: React.FC<SelectionTokenProps> = ({
    pageInfo,
    blockDescriptors,
    color,
    onMouseDown = () => {},
    onContextMenu = () => {},
}: SelectionTokenProps) => {
    return (
        <>
            {blockDescriptors.map((blockDescriptor) => {
                const b = pageInfo.getScaledBlockBounds(pageInfo.blocks[blockDescriptor.blockIndex]);
                return (
                    <div
                        className="selection-token"
                        key={`selection-token-${blockDescriptor.blockIndex}`}
                        style={{
                            left: `${b.left}px`,
                            top: `${b.top}px`,
                            width: `${b.right - b.left}px`,
                            height: `${b.bottom - b.top}px`,
                            position: 'absolute',
                            background: color,
                            opacity: '0.2',
                            borderRadius: '3px',
                        }}
                        onMouseDown={onMouseDown}
                        onContextMenu={onContextMenu}
                    />
                );
            })}
        </>
    );
};
