import pdfjs from 'pdfjs-dist';
import { Annotation } from '../context/SsieAnnotationStore';
import { Bounds } from './Bounds';
import { BlockDescriptor } from './BlockDescriptor';
import { getBlockBounds, Optional, spanningBound } from '../utils/SsieUtils';
import { Block } from './Block';

/**
 * Object containing a PDF page's information such as its bounds, dimensions, and blocks.
 */
export class PDFPageInfo {
    public bounds?: Bounds = undefined;

    constructor(
        public readonly pdfPageProxy: pdfjs.PDFPageProxy,
        public readonly blocks: Block[] = [],
        public readonly width: number,
        public readonly height: number
    ) {}

    /**
     * Create annotation from partial BlockDescriptor object
     * @param partialBlockDescriptor 
     * @param tag 
     * @param blocks Blocks to used instead of the PDFPageInfo's blocks (intended for usage with arbitration)
     * @returns Annotation object or undefined
     */
    createAnnotationFromPartialBlockDescriptor(partialBlockDescriptor: { index: number; startOffset?: number; endOffset?: number }[], tag: string, blocks?: Block[]): Optional<Annotation> {
        const blockDescriptors: BlockDescriptor[] = [];
        const blockBounds: Bounds[] = [];
        for (let i = 0; i < partialBlockDescriptor.length; i++) {
            const block = (blocks ? blocks : this.blocks)[partialBlockDescriptor[i].index];
            
            // the annotation's related block was marked as invalid, don't create the annotation
            if (block.invalid) {
                return undefined;
            }

            const blockBound = getBlockBounds(block, this.width, this.height);
            const blockDescriptor = new BlockDescriptor(
                this.pdfPageProxy.pageNumber - 1,
                partialBlockDescriptor[i].index,
                block.BlockType,
                partialBlockDescriptor[i].startOffset ? partialBlockDescriptor[i].startOffset! : 0,
                partialBlockDescriptor[i].endOffset ? partialBlockDescriptor[i].endOffset! : block.Text.length
            );
            blockDescriptors.push(blockDescriptor);
            blockBounds.push(blockBound);
        }
        if (blockDescriptors.length === 0) {
            return undefined;
        }
        const bounds = spanningBound(blockBounds);
        return new Annotation(bounds, this.pdfPageProxy.pageNumber, tag, blockDescriptors);
    }

    getAnnotationForBounds(selection: Bounds, tag: string, blockDescriptorIds?: Set<string>): Optional<Annotation> {
        /* This function is quite complicated. Our objective here is to
           compute overlaps between a bounding box provided by a user and
           grobid token spans associated with a pdf. The complexity here is
           that grobid spans are relative to an absolute scale of the pdf,
           but our user's bounding box is relative to the pdf rendered in their
           client. 

           The critical key here is that anything we *store* must be relative
           to the underlying pdf. So for example, inside the for loop, we are
           computing: 
           
           whether a grobid token (tokenBound), scaled to the current scale of the
           pdf in the client (scaled(tokenBound, this.scale)), is overlapping with
           the bounding box drawn by the user (selection).

           But! Once we have computed this, we store the grobid tokens and the bound
           that contains all of them relative to the *original grobid tokens*.

           This means that the stored data is not tied to a particular scale, and we
           can re-scale it when we need to (mainly when the user resizes the browser window).
         */

        if (this.bounds === undefined) {
            throw new Error('Unknown Page Bounds');
        }
        const blockDescriptors: BlockDescriptor[] = [];
        const blockBounds: Bounds[] = [];
        for (let i = 0; i < this.blocks.length; i++) {
            const block = this.blocks[i];
            const blockBound = getBlockBounds(block, this.width, this.height);
            if (doOverlap(scaleBounds(blockBound, this.scale), selection)) {
                const blockDescriptor = new BlockDescriptor(this.pdfPageProxy.pageNumber - 1, i, block.BlockType, 0, this.blocks[i].Text.length);
                if (!blockDescriptorIds || !blockDescriptorIds.has(blockDescriptor.id)) {
                    blockDescriptors.push(blockDescriptor);
                    blockBounds.push(blockBound);
                }
            }
        }
        if (blockDescriptors.length === 0) {
            return undefined;
        }
        const bounds = spanningBound(blockBounds);
        return new Annotation(bounds, this.pdfPageProxy.pageNumber, tag, blockDescriptors);
    }

    getScaledBlockBounds(block: Block): Bounds {
        return this.getScaledBounds(getBlockBounds(block, this.width, this.height));
    }

    getScaledBounds(b: Bounds): Bounds {
        return scaleBounds(b, this.scale);
    }

    get scale(): number {
        if (this.bounds === undefined) {
            throw new Error('Unknown Page Bounds');
        }
        const pdfPageWidth = this.width;
        const domPageWidth = this.bounds.right - this.bounds.left;
        return domPageWidth / pdfPageWidth;
    }
}

/**
 * Returns the provided bounds scaled by the provided factor.
 */
function scaleBounds(bounds: Bounds, scale: number): Bounds {
    return {
        left: bounds.left * scale,
        top: bounds.top * scale,
        right: bounds.right * scale,
        bottom: bounds.bottom * scale,
    };
}

/**
 * Returns true if the provided bounds overlap.
 */
export function doOverlap(a: Bounds, b: Bounds): boolean {
    if (a.left >= b.right || a.right <= b.left) {
        return false;
    } else if (a.bottom <= b.top || a.top >= b.bottom) {
        return false;
    }
    return true;
}

export default PDFPageInfo;
